@import url(https://fonts.googleapis.com/css2?family=Mukta&display=swap);
.app-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
}

.API-status {
  color: var(--green);
  padding: 1rem;

  position: fixed;
  right: 0;
  bottom: 0;
}

.single-product {
  display: flex;
  flex-flow: row wrap;
  margin-top: 5rem;
}

.single-product img {
  width: 500px;
  height: 500px;
  border-radius: 10px;
  margin-right: 5rem;
}

.product-details {
  display: flex;
  flex-flow: column wrap;
  width: 40vw;
}

.product-inStock {
  font-style: italic;
}

.single-product-quantity-group {
  display: flex;
  flex-flow: column wrap;
}

.single-product-quantity-button {
  margin-top: 2rem;
  font-size: 18px;
}

.single-product-add-to-cart {
  width: 10vw;
}

.all-products {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
}

.products-list {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;

  border: 2px solid black;
  padding: 5px;
  margin: 10px;
  width: 264px;

  box-shadow: 2px 2px 5px lightgray;
}

.product-image {
  width: 250px;
  height: 250px;
}

.product-name {
  font-size: 24px;
  text-decoration: underline;
}

.product-name:hover {
  cursor: pointer;
}

.product-image:hover {
  cursor: pointer;
}

.navbar {
  display: flex;
  flex-flow: row wrap;
  justify-content: space-evenly;
  align-items: center;

  width: 90vw;
  border: 2px solid black;

  font-size: 24px;
  margin: 5px;
  padding: 5px;
}

.navbar-links {
  text-decoration: none;
}

.navbar-links:hover {
  text-decoration: underline;
}

/* import fonts here */

* {
  box-sizing: border-box;
}

:root {
  /* define variables here */
  --green: green;
  --red: red;
  --blue: blue;
}

html {
  touch-action: manipulation;
}

body {
  margin: 0;
  padding: 0;
  height: 100vh;
  width: 100vw;
  font-family: 'Mukta', sans-serif;
}

